<template>
  <div class="login">
    <div class="loginBg">
      <div class="login-modal">
        <div>
          <h3 class="login-modal-title">
            管理员用户登录
          </h3>
          <el-form ref="formValidate" :model="formValidate" :rules="ruleValidate" >
            <el-form-item prop="name">
              <el-input v-model="formValidate.name" placeholder="请输入管理员用户名">
                <i class="el-icon-iconfont icon-person" slot="prepend"></i>
              </el-input>
            </el-form-item>
            <el-form-item prop="password">
              <el-input v-model="formValidate.password" @keyup.native.enter="handleSubmit('formValidate')" type="password" placeholder="请输入密码">
                <i class="el-icon-iconfont icon-icon2" slot="prepend"></i>
              </el-input>
            </el-form-item>
            <el-row>
              <el-col :span="24" >
                <a class="login-forget" @click="toRegister">忘记密码</a>
              </el-col>
            </el-row>
          </el-form>
        </div>
        <div class="login-footer">
          <el-button size="medium" :loading="modal_loading" @click="handleSubmit('formValidate')">登录</el-button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    name: 'login',
    data () {
      var validateName = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入手机号'))
        } else {
          if (value === 'admin') {
            callback()
          } else if (!(/^1(3|4|5|7|8)\d{9}$/.test(value))) {
            callback(new Error('请输入正确的手机号'))
          } else {
            callback()
          }
        }
      }
      return {
        modal_loading: false,
        formValidate: {
          name: '',
          password: ''
        },
        ruleValidate: {
          name: [
            {validator: validateName, trigger: 'blur'}
          ],
          password: [
            {required: true, message: '密码不能为空', trigger: 'blur'}
          ]
        }
      }
    },
    methods: {
      handleSubmit (name) { // login
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.modal_loading = true
            setTimeout(() => {
              this.modal_loading = false
              if ((this.formValidate.name === 'admin') && (this.formValidate.password === '123456')) {
                sessionStorage.token = this.formValidate.password
                this.$router.push('/index')
              } else {
                this.$message.error('账号或密码错误!')
              }
            }, 1000)
          }
        })
      },
      toRegister () {
        this.$router.push('/register')
      }
    }
  }
</script>





